.button-select {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin-bottom: 40px;
}

.button-select button {
    padding: 10px 20px;
    text-transform: uppercase;
    font-size: 12px;
    background: #fb5;
    border: none;
    color: #ffffff;
}

.button-select button:hover {
    background: #fa5;
}



